@import "../../styles/variables";
@import "../../styles/mixins";

*{
  font-family: "Montserrat", sans-serif;
};

.instruction {
  @include centralColumnFlexPositionHWHundred;

  .instruction_container{
    @include centralFlexPosition;
    @include flexColumn;
    width: 1020px;
    height: 100%;

    .instruction_container_header{
      @include centralColumnFlexPositionHWHundred;
      padding-top: 10%;

      .instruction_container_header_title, .instruction_container_header_text{
        @include centralColumnFlexPositionHWHundred;
      };

      .instruction_container_header_title h1{
        color: $extensionInstructionColor;
        font-style: Bold;
        font-size: 59px;
        line-height: 72px;
        line-height: 100%;
      };

      .instruction_container_header_text{
        @include centralColumnFlexPositionHWHundred;
        padding: 5% 0px 0px 0px;
      };

      .instruction_container_header_text p{
        color: $extensionInstructionColor;
        font-style: Regular;
        font-size: 25px;
        line-height: 30px;
        line-height: 100%;
        text-align: center;
        margin: 0px;
      };

    };

    .instruction_container_start{
      @include centralColumnFlexPositionHWHundred;
      position: relative;
      z-index: 5;

      .start-free{
        @include buttonCorrect;
        @include normalBoldFont;
        background-color: $startBtnColor;
        color: $color;
        height: 89.52px;
        width: 483px;
        border-radius: 110px;
        font-size: 28px;
        line-height: 34px;
        margin-top: 100px;
        transition: all 0.3s linear;
      };

      .start-free:hover{
        background-color: #ffb43f;
      };

    };

  };

  .instruction_container_main{
    padding: 100px 0px 100px 0px;

    .instruction_container_main_text-card{
      @include centralAlignFlex;
      justify-content: space-between;
      width: 100%;

      .instruction_container_main_text-card_a, .instruction_container_main_text-card_b{
        @include flexColumn;
        width: 485px;
        height: 465px;
        border-radius: 13px;
        border: $extensionInstructionColor solid;
      };

      .instruction_container_main_text-card_b .card_header{
        margin-bottom: 60px;
      };

      .card_title{
        padding-top: 5%;
      };

      .card_folder_content{
        padding-top: 10px;
      };

      .card_folder_top{
        @include centralAlignFlex;
        color: $blackColor;
      };

      .card_folder h2, .card_folder p{
        color: $blackColor;
        font-size: 15px;
      };

      .card_folder h2{
        font-weight: 700;
        padding: 2% 0 2% 0;
      };

      .card_header{
        @include flexColumn;
        align-items: flex-start;
        justify-content: flex-start;
        width: 100%;
        padding-left: 30px;
      };

      .card_header h2{
        text-transform: uppercase;
        line-height: 23px;
        font-size: 19px;
        font-weight: 700;
        color: $blackColor;
      };

      .card_folder{
        @include flexColumn;
        width: 100%;
        padding-left: 30px;

        .card_folder_content{
          display: inline-flex;
          width: 100%;

          .card-img-a{
            @include wHHundred;
            margin-left: -15px;
          };

          .card-img-b{
            width: 45px;
            height: 45px;
          };

        };

        .card_folder_content p{
          display: flex;
          padding-left: 30px;
          color: $blackColor;
        };

        .card_folder_content p:nth-child(1){
          padding: 0;
        };

      };

      .card_footer a{
        @include centralFlexPosition;
        @include normalBoldFont;
        color: $extensionInstructionColor;
        text-decoration-line: underline;
        font-size: 16px;
        line-height: 20px;
        padding-right: 50px;
      };

      .card_footer{
        display: inline-flex;
        width: 100%;
        justify-content: space-around;
        padding-top: 30px;

        .register{
          @include buttonCorrect;
          @include normalBoldFont;
          width: 221px;
          margin-right: 15px;
          background: linear-gradient(179.1deg, #6178F5 22.38%, #195AA9 82.85%);
          border-radius: 5px;
          height: 40px;
          font-size: 16px;
          line-height: 20px;
          color: $color;
        };

      };

    };

    .instruction_container_main_step h3{
      font-size: 19px;
    };

    .instruction_container_main_step{
      @include wHHundred;
      display: flex;
      position: relative;
      z-index: 5;
      margin-top: 160px;
      border-radius: 13px;
      background: linear-gradient(175.62deg, rgba(97, 120, 245, 0.1) 22.38%, rgba(25, 90, 169, 0.1) 82.85%);

      .instruction_container_main_step_right, .instruction_container_main_step_left{
        @include flexColumn;
        justify-content: space-between;
        width: 50%;
        height: 100%;
        padding-bottom: 3%;
      };

      .instruction_container_main_step_left{
        padding-left: 30px;
      };

      .instruction_container_main_step_left .step_section:nth-child(2),
      .instruction_container_main_step_right .step_section:nth-child(2),
      .instruction_container_main_step_left .step_section:nth-child(2){
        padding-top: 150px;
      };

      .instruction_container_main_step_left .step_section:nth-child(2) .step_section_main{
        padding-top: 50px;
      };

      .instruction_container_main_step_right .step_section:nth-child(2)
      .step_section_main img{
        @include wHHundred;
        padding: 8% 3% 0 3%;
      };

      .instruction_container_main_step_right{
        padding-left: 6%;
      };

      .step_section{
        padding-top: 20px;
        width: 100%;
      };

      .step_section_header{
        display: inline-flex;
      };

      .step_section_header h3{
        color: $blackColor;
        text-transform: uppercase;
        font-weight: 700;
        padding-left: 30px;
      };

      .step_section_header_text{
        padding-right: 10px;
      };

      .step_section_header_image{
        align-items: center;
        justify-content: center;
      };

      .step_section_header_image img{
        height: 42px;
        width: 42px;
        margin-top: 2px;
      };

      .step_section_main img:nth-child(1){
        width: 85%;
        padding-top: 60px;
        padding-left: 80px;
        position: relative;
        z-index: 5;
      };

      .step_section_main h2{
        color: $blackColor;
        padding-top: 20px;
      };

      .step_section_main h2 span{
        background-color: #6078F4;
        color: $color;
      };

      .step-arrow{
        width: 40%;
        position: relative;
        margin: -90px 0px -40px 75%;
      };

    };

  };

  .instruction_container_footer{
    @include centralColumnFlexPositionHWHundred;
  };

  .instruction_footer_background{
    background: no-repeat bottom/105% url("../../assets/images/extension-instruction/backgroundFlowers.svg");
    display: flex;
    width: 100%;
    height: 600px;
    position: relative;
    z-index: 1;
    margin-top: -420px;
  };

};