@charset "UTF-8";
body {
  padding: 0;
  margin: 0; }

.Privacy {
  margin-bottom: 2vh;
  padding: 1% 8%; }
  .Privacy p {
    margin: 5px !important; }
  .Privacy ul {
    padding-left: 20px; }
  .Privacy li {
    font-size: 1.2rem; }
  .Privacy h1 {
    margin-bottom: 20px; }
  .Privacy h2 {
    margin-bottom: 20px; }
  .Privacy h3 {
    text-align: center;
    font-size: 1.8rem; }
  .Privacy h4 {
    text-align: center;
    font-size: 1.4rem;
    margin-bottom: 10px; }
  .Privacy #footer p {
    text-align: right; }
  .Privacy .first_p {
    padding-bottom: 10px; }
  .Privacy .google-link {
    color: #1E4470;
    text-decoration: underline; }

ol {
  list-style-type: none;
  counter-reset: item; }
  ol li::before {
    content: counter(item) ") ";
    /* Добавляем к числам скобку */
    counter-increment: item;
    /* Задаём имя счетчика */ }
